import styled from "styled-components";

export const Item = styled.div`
    display: flex;
`;

export const LeftContent = styled.div`
    img {
        width: 350px;
        height: 350px;
    }
`;

export const RightContent = styled.div`
    padding: 20px 20px;
    margin: 0 auto;
    h6 {
        font-weight: normal;
        font-size: 13px;
    }
    .price {
        font-weight: bold;
    }
    .description {
        font-size: 13px;
    }
`;
