import React from "react";
import { Button, Modal, Form, Row, Col } from "react-bootstrap";
import { useState } from "react";
import { Container, Layout, Logo, SearchBar } from "./Header.style";
import { Wrapper } from "../../GlobalStyle";
import "./Header.css";
const Header = (props) => {
    const { handleAddItem, handleSearchButton } = props;
    const [modalShow, setModalShow] = React.useState(false);
    const [value, setValue] = useState("");
    const initialItem = {
        id: null,
        name: "",
        description: "",
        price: null,
        brand: "",
        amount: null,
        imgUrl: "",
    };
    const [item, setItem] = useState(initialItem);
    const handleAddButton = () => {
        handleAddItem(item);
        setItem(initialItem);
        setModalShow(false);
    };
    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setItem({ ...item, [name]: value });
    };
    return (
        <>
            <Container>
                <Wrapper>
                    <Layout>
                        <Logo>
                            <a href="https://my-app123sad.herokuapp.com/">
                                {" "}
                                <img
                                    alt={""}
                                    src="https://salt.tikicdn.com/ts/upload/ae/f5/15/2228f38cf84d1b8451bb49e2c4537081.png"
                                />
                                <img
                                    alt={""}
                                    src="https://salt.tikicdn.com/ts/upload/e5/1d/22/61ff572362f08ead7f34ce410a4a6f96.png"
                                />
                            </a>
                        </Logo>
                        <SearchBar>
                            <input
                                placeholder="Search"
                                onChange={(event) => setValue(event.target.value)}
                                onKeyPress={(e) => e.key === "Enter" && handleSearchButton(value)}
                            />
                            <div
                                onClick={() => {
                                    handleSearchButton(value);
                                }}
                            >
                                <img
                                    alt={""}
                                    src="https://salt.tikicdn.com/ts/upload/ed/5e/b8/8538366274240326978318348ea8af7c.png"
                                />
                            </div>
                        </SearchBar>
                        <div style={{ display: "flex" }}>
                            <div className="button" onClick={() => setModalShow(true)}>
                                <span className="button__mask"></span>
                                <span className="button__text">Add Item</span>
                                <span className="button__text button__text--bis">Add Item</span>
                            </div>
                            {/* <Button variant="primary" >
                                Add
                            </Button>{" "} */}
                            <MyVerticallyCenteredModal
                                show={modalShow}
                                onHide={() => setModalShow(false)}
                                handleAddButton={handleAddButton}
                                handleInputChange={handleInputChange}
                                item={item}
                            />
                        </div>
                    </Layout>
                </Wrapper>
            </Container>
        </>
    );
};

function MyVerticallyCenteredModal(props) {
    return (
        <Modal {...props} size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">Add Item</Modal.Title>
            </Modal.Header>
            <Form>
                <Modal.Body>
                    <Form.Group as={Row} className="mb-3" controlId="formHorizontalEmail">
                        <Form.Label column sm={2}>
                            Name
                        </Form.Label>
                        <Col sm={10}>
                            <Form.Control
                                type=""
                                placeholder=""
                                name="name"
                                value={props.item.name}
                                onChange={props.handleInputChange}
                            />
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} className="mb-3" controlId="formHorizontalPassword">
                        <Form.Label column sm={2}>
                            Description
                        </Form.Label>
                        <Col sm={10}>
                            <Form.Control
                                type=""
                                placeholder=""
                                onChange={props.handleInputChange}
                                name="description"
                                value={props.item.description}
                            />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} className="mb-3" controlId="formHorizontalPassword">
                        <Form.Label column sm={2}>
                            Price
                        </Form.Label>
                        <Col sm={10}>
                            <Form.Control
                                type=""
                                placeholder=""
                                name="price"
                                value={props.item.price}
                                onChange={props.handleInputChange}
                            />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} className="mb-3" controlId="formHorizontalPassword">
                        <Form.Label column sm={2}>
                            Brand
                        </Form.Label>
                        <Col sm={10}>
                            <Form.Control
                                type=""
                                placeholder=""
                                name="brand"
                                value={props.item.brand}
                                onChange={props.handleInputChange}
                            />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} className="mb-3" controlId="formHorizontalPassword">
                        <Form.Label column sm={2}>
                            Amount
                        </Form.Label>
                        <Col sm={10}>
                            <Form.Control
                                type=""
                                placeholder=""
                                name="amount"
                                value={props.item.amount}
                                onChange={props.handleInputChange}
                            />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} className="mb-3" controlId="formHorizontalPassword">
                        <Form.Label column sm={2}>
                            Image Url
                        </Form.Label>
                        <Col sm={10}>
                            <Form.Control
                                type=""
                                placeholder=""
                                name="imgUrl"
                                value={props.item.imgUrl}
                                onChange={props.handleInputChange}
                            />
                        </Col>
                    </Form.Group>
                </Modal.Body>

                <Modal.Footer>
                    <Button onClick={props.handleAddButton}>Add</Button>

                    <Button onClick={props.onHide}>Close</Button>
                </Modal.Footer>
            </Form>
        </Modal>
    );
}

export default Header;
