import styled from "styled-components";

export const Container = styled.div`
    background-color: #1a94ff;
`;

export const Layout = styled.div`
    display: flex;
    padding: 15px 0px;
    justify-content: center;
    gap: 30px;
    align-items: center;
`;

export const Logo = styled.div`
    a {
        display: flex;
        flex-direction: column;
        gap: 9px;
        img {
            width: 60px;
        }
    }
`;

export const SearchBar = styled.div`
    display: flex;
    align-items: center;
    outline: none;

    div {
        background-color: #0d5cb6;
        padding: 0px 5px;
        align-items: center;
        height: 40px;
        img {
            height: 40px;
            cursor: pointer;
        }
    }
    input {
        border: none;
        height: 40px;
        width: 600px;
        padding: 0px 10px;
        outline: none;
    }
`;
